// auto-translate.service.ts
import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Injectable({ providedIn: 'root' })
export class AutoTranslateService {
  private translations: Record<string, Record<string, string>> = {};

  constructor(private translate: TranslateService) {
    this.initDefaultTranslations();
  }

  private initDefaultTranslations() {
    this.translations = {
      // Titres
      Profil: {
        en: 'Profile',
        fr: 'Profil',
        pt: 'Perfil'
      },
      Settings: {
        en: 'Settings',
        fr: 'Paramtres',
        pt: 'Configuraes'
      },
      Language: {
        en: 'Language',
        fr: 'Langue',
        pt: 'Idioma'
      },
      Country: {
        en: 'Country',
        fr: 'Pays',
        pt: 'Pas',
        es: 'Pas'
      },
      
      // Descriptions
      'Grez vos informations personnelles, votre avatar et vos prfrences de compte': {
        en: 'Manage your personal information, avatar and account preferences',
        fr: 'Grez vos informations personnelles, votre avatar et vos prfrences de compte',
        pt: 'Gerencie suas informaes pessoais, avatar e preferncias de conta'
      },
      'Configurez les paramtres de l\'application et personnalisez votre exprience': {
        en: 'Configure application settings and customize your experience',
        fr: 'Configurez les paramtres de l\'application et personnalisez votre exprience',
        pt: 'Configure as definies do aplicativo e personalize sua experincia'
      },
      'Choisissez votre langue prfre pour l\'interface de l\'application': {
        en: 'Choose your preferred language for the application interface',
        fr: 'a pour l\'interface de l\'application',
        pt: 'Escolha seu idioma preferido para a interface do aplicativo'
      },
      'Select your country for regional settings and phone number formatting': {
        en: 'Select your country for regional settings and phone number formatting',
        fr: 'Slectionnez votre pays pour les paramtres rgionaux et le format des numros de tlphone',
        pt: 'Selecione seu pas para configuraes regionais e formatao de nmeros de telefone',
        es: 'Seleccione su pas para configuraciones regionales y formato de nmeros telefnicos'
      },
    };
  }

  translateText(text: string): string {
    const currentLang = this.translate.currentLang || 'en';
    const translation = this.translations[text]?.[currentLang];

    if (!translation) {
      console.warn(`Missing translation for "${text}" in ${currentLang}`);
      return text;
    }

    return translation;
  }
}